export default {
	data() {
		return {
			circleList: 24, //圆点
			awardList: [], //奖品数组
			indexSelect: 0, //被选中的奖品index
			isRunning: false, //是否正在抽奖,
		}
	},
	methods: {
		//获取随机数
		getRandom: function(u) {
			let rnd = Math.random() > 0.5 ? "2" : "1";
			u = u || 3;
			for (var i = 0; i < u; i++) {
				rnd += Math.floor(Math.random() * 10);
			}
			console.log(rnd);
			return Number(rnd);
		},
		//开始抽奖
		async startDrawing() {
			if (this.isRunning) return
			this.isRunning= true;
			let res = await this.$fetch(this.$api.executeAward,{},'GET')
			console.log(res);
			let indexSelect = 0;
			let i = 0;
			let randomNum = this.getRandom(3);
			let timer = setInterval(() => {
				++indexSelect;
				//这里用y=30*x+150函数做的处理.可根据自己的需求改变转盘速度
				indexSelect = indexSelect % 8;
				this.indexSelect= indexSelect;
				i += 40;
				if ( i > randomNum && this.awardList[indexSelect].id ==res.data.id ) {
					//去除循环
					clearInterval(timer)
					timer = null;
					//获奖提示
					uni.showModal({
						title: '恭喜您',
						content: '获得了奖品【'+this.awardList[indexSelect].name+'】',
						confirmColor: '#5677FC',
						showCancel: false, //去掉取消按钮
						success: (res) => {
							if (res.confirm) {
								this.isRunning= false
							}
						}
					})
				}
			}, (70 + i))
		},
		// 获取抽奖奖品列表
		async getAwardList(){
			let res = await this.$fetch(this.$api.getAwardList,{},'GET')
			console.log(res);
			this.awardList = res.data
		}
	},
	created() {
		this.getAwardList()
	}
}
